import { Form, Input } from 'antd'
import { useSelector } from 'react-redux'
import { adapterLoginEntities } from '@/store/registerSlice/index.js'
import { useLocation } from 'react-router-dom'

const PhoneNumber = ({ tailLayout }) => {
  let location = useLocation()
  let loginData = useSelector(adapterLoginEntities)

  return (
    <div>
      <Form.Item
        {...tailLayout}
        name="phoneNo"
        rules={[
          {
            validator: (rule, value) => {
              if (!value) return Promise.reject(new Error(''))
              const reg = /^1[3456789]\d{9}$/
              if (!reg.test(value)) return Promise.reject(new Error(''))
              if (loginData[value] && location.pathname === '/register')
                return Promise.reject(new Error(''))
              return Promise.resolve()
            },
          },
        ]}
      >
        <Input placeholder="" />
      </Form.Item>
    </div>
  )
}

export default PhoneNumber
