import React from 'react'
import { Button, Form, Input, Space, message } from 'antd'

const PhoneCode = ({ tailLayout, formObj }) => {
  const [messageApi, contextHolder] = message.useMessage()

  const sendPhoneCode = async () => {
    try {
      await formObj.validateFields(['phoneNo'])
      // 
      messageApi.open({
        type: 'success',
        content: '',
      })
    } catch (err) {
      console.log(err)
    }
  }

  return (
    <div>
      {contextHolder}
      <Form.Item
        {...tailLayout}
        name="phoneCode"
        rules={[
          {
            validator: (rule, value) => {
              if (!value) return Promise.reject(new Error(''))
              const reg = /^\d{6}$/
              if (!reg.test(value)) return Promise.reject(new Error('6'))
              return Promise.resolve()
            },
          },
        ]}
      >
        <Space.Compact size="large">
          <Input placeholder="" />
          <Button type="primary" style={{ backgroundColor: '#14a900' }} onClick={sendPhoneCode}>
            
          </Button>
        </Space.Compact>
      </Form.Item>
    </div>
  )
}

export default PhoneCode
