import './Header.less'
import logo from '@/assets/imgs/logo.JPG'
import { Link } from 'react-router-dom'
import { Col, Row, Space } from 'antd'
import { useEffect, useState } from 'react'
import NavSearch from '../navSearch/NavSearch'
import NavHorizontal from '../navHorizontal/NavHorizontal'
import LeftBar from '../leftBar/LeftBar'
import CommonButton from '../commonButton/CommonButton'

function TopHeader() {
  const [suctionCeiling, setSuctionCeiling] = useState(false)
  const [oldTop, setOldTop] = useState(0)

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, true)
    return window.removeEventListener('scroll', handleScroll)
  })

  const handleScroll = () => {
    let y = document.documentElement.scrollTop
    if (oldTop > y) {
      setOldTop(y)
      return setSuctionCeiling(false)
    }
    setOldTop(y)
    setSuctionCeiling(true)
  }

  return (
    <div
      className={[
        'top-header-container',
        suctionCeiling ? 'top-header-container-shadow' : 'top-header-scroll-top-container-shadow',
      ].join(' ')}
    >
      <header>
        <Row className="header-container">
          <Col span={12} className="left--top-bar">
            <LeftBar />
          </Col>
          <Col span={12} className="right-btn">
            <Space>
              <CommonButton title="" />
              <CommonButton navUrl="/login" title="" />
              <CommonButton navUrl="/register" title="" emphasize />
            </Space>
          </Col>
        </Row>
      </header>

      <Row className="nav-bar">
        <Col span={4}>
          <Link to="/">
            <img className="logo-img" src={logo} alt="" />
          </Link>
        </Col>
        <Col span={9}>
          <NavHorizontal />
        </Col>
        <Col span={6} offset={5}>
          <NavSearch />
        </Col>
      </Row>
    </div>
  )
}

export default TopHeader
