import './HomePage.less'
import { Space } from 'antd'

import CommonButton from '../components/commonButton/CommonButton'
import DesignPart from './components/designPart/DesignPart'
import WorksPart from './components/worksPart/WorksPart'
import DesignerInfo from '@/pages/components/designerInfo/DesignerInfo'

const Index = () => {
  return (
    <div className="home-page-container">
      <section className="home-body">
        <h2 className="homepage-title-h2">  </h2>
        <h3 className="homepage-title-h3">300100+</h3>
        <div className="homepage-title-btn">
          <Space size="large">
            <CommonButton
              title=""
              size="large"
              padNumber="30"
              emphasize
              navUrl="/login"
            />
            <CommonButton title="" size="large" padNumber="30" navUrl="/login" />
          </Space>
        </div>
        <p className="homepage-title-text">5</p>
        <DesignPart />
        <WorksPart />
        <h2 className="freelance-designers"></h2>
        <DesignerInfo />

        <div className="adv-team-container">
          <h2 className="adv-team-p1"></h2>
          <p className="adv-team-p2"></p>
          <CommonButton
            title=""
            size="large"
            padNumber="40"
            emphasize
            fontEmphasize
            fontSize="16"
            bgColor="#fff"
            color="#14A900"
            navUrl="/login"
          />
        </div>
      </section>
    </div>
  )
}

export default Index
